<template>
  <div>
    <el-container>
      <el-header style="text-align: right"></el-header>
      <el-container>
        <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
          <el-menu :default-openeds="['1']" router="true">
            <el-submenu index="1">
              <template slot="title"
                ><i class="el-icon-message"></i>菜单</template
              >
              <el-menu-item-group>
                <el-menu-item index="/teacher_homepage_personal_info"
                  >个人信息</el-menu-item
                >
                <el-menu-item index="/teacher_courses_manage"
                  >课程管理</el-menu-item
                >
                <el-menu-item index="/teacher_course_learning_progress">课程情况</el-menu-item>
              </el-menu-item-group>
            </el-submenu>
          </el-menu>
        </el-aside>

        <el-main>
          <el-button type="primary" @click="dialogFormVisible = true"
            >新增课程</el-button
          >
          <el-table :data="courses" style="width: 100%">
            <el-table-column prop="courseID" label="课程ID" width="180">
            </el-table-column>
            <el-table-column prop="courseName" label="课程名" width="180">
            </el-table-column>
            <el-table-column prop="description" label="课程介绍">
            </el-table-column>
            <el-table-column
              prop="startDateTime"
              :formatter="formatStartDateTime"
              label="起始日期"
            >
            </el-table-column>
            <el-table-column
              prop="endDateTime"
              :formatter="formatEndDateTime"
              label="结课日期"
            >
            </el-table-column>
            <el-table-column prop="maxStudents" label="可报名学生人数">
            </el-table-column>
            <el-table-column label="操作" width="180">
              <template slot-scope="scope">
                <el-button size="mini" @click="coursePage(scope.row)"
                  >课程章节编辑页面</el-button
                >
                <el-button
                  type="danger"
                  size="mini"
                  @click="deleteCourse(scope.row.courseID)"
                  >删除</el-button
                >
              </template>
            </el-table-column>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
    <el-dialog title="填写课程信息" :visible.sync="dialogFormVisible">
      <el-form :model="newCourse" :rules="rules" ref="newCourse">
        <el-form-item
          label="课程名"
          prop="courseName"
          :label-width="formLabelWidth"
        >
          <el-input
            v-model="newCourse.courseName"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item
          label="开始时间"
          prop="startDateTime"
          :label-width="formLabelWidth"
        >
          <el-date-picker
            v-model="newCourse.startDateTime"
            type="datetime"
            :picker-options="startPickerOptions"
            @change="handleStartDateChange"
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item
          label="结束时间"
          prop="endDateTime"
          :label-width="formLabelWidth"
        >
          <el-date-picker
            v-model="newCourse.endDateTime"
            type="datetime"
            :picker-options="endPickerOptions"
          >
          </el-date-picker>
        </el-form-item>

        <el-form-item
          label="可报名学生人数"
          prop="maxStudent"
          :label-width="formLabelWidth"
        >
          <el-input v-model="newCourse.maxStudents"> </el-input>
        </el-form-item>

        <el-form-item
          label="描述"
          prop="description"
          :label-width="formLabelWidth"
        >
          <el-input v-model="newCourse.description" type="textarea"> </el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitForm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
    
    <script>
import axios from "axios";

//   import axios from "axios";

export default {
  data() {
    return {
      courses: [],
      dialogFormVisible: false,
      newCourse: {
        teacherID: localStorage.getItem("id"),
        courseName: "",
        startDateTime: "",
        endDateTime: "",
        maxStudents: "",
        description: "",
      },
      rules: {
        courseName: [
          { required: true, message: "请输入课程名", trigger: "blur" },
        ],
        startDateTime: [
          { required: true, message: "请选择日期", trigger: "blur" },
        ],
        endDateTime: [
          { required: true, message: "请输入日期", trigger: "blur" },
        ],
        maxStudents: [
          {
            required: true,
            message: "请输入数字",
            trigger: "blur",
          },
          {
            pattern: /^\d+$/,
            message: "只能输入数字",
            trigger: "blur",
          },
        ],
        description: [
          { required: true, message: "输入课程描述", trigger: "blur" },
        ],
      },
      startPickerOptions: {
        // 起始日期选择器的选项
        disabledDate: (time) => {
          if (this.newCourse.endDateTime) {
            // 如果已经选择了结束日期，则禁用起始日期晚于结束日期的日期
            return (
              time.getTime() > new Date(this.newCourse.endDateTime).getTime()
            );
          }
          return false;
        },
      },
      endPickerOptions: {
        // 结束日期选择器的选项
        disabledDate: (time) => {
          if (this.newCourse.startDateTime) {
            // 如果已经选择了起始日期，则禁用结束日期早于起始日期的日期
            return (
              time.getTime() < new Date(this.newCourse.startDateTime).getTime()
            );
          }
          return false;
        },
      },
    };
  },
  methods: {
    submitForm() {
      this.$refs.newCourse.validate((valid) => {
        if (valid) {
          // 表单验证通过，可以提交表单数据
          console.log("表单验证通过，可以提交表单数据");

          axios
            .post(
              "http://localhost:8080/SongGuo_Learning/teacher/addCourse",
              this.newCourse,
              {
                headers: {
                  Token: localStorage.getItem("token"),
                },
              }
            )
            .then((resp) => {
              if (resp.data.message == "ADD_OK") {
                (this.dialogFormVisible = false),
                  this.$notify.success({
                    title: "添加成功",
                    message: "您已成功添加一个新课程!请刷新页面更新数据...",
                  });
              }
              console.log(resp.data);
            });
        } else {
          // 表单验证失败
          console.log("表单验证失败，请检查输入内容");
          return false;
        }
      });
    },
    handleStartDateChange(value) {
      // 在起始日期改变时更新结束日期的可选范围
      this.$nextTick(() => {
        if (value && this.newCourse.endDateTime) {
          // 如果已经选择了结束日期，并且起始日期改变，则重新校验结束日期是否早于起始日期
          if (
            new Date(this.newCourse.endDateTime).getTime() <
            new Date(value).getTime()
          ) {
            this.newCourse.endDateTime = "";
          }
        }
      });
    },
    deleteCourse(courseID) {
      // 删除课程信息
      this.$confirm("确认删除此课程? 所有章节和题目信息都将被同步删除!")
        .then(() => {
          axios.get("http://localhost:8080/SongGuo_Learning/teacher/deleteCourse?courseID=" + courseID, {
            headers: {
              Token: localStorage.getItem("token")
            }
          }).then(resp => {
            console.log(resp.data);
            if (resp.data.message == 'DELETE_OK') {
              this.$notify.success({
                title: "操作成功",
                message: "您已成功删除一套课程,请刷新页面..."
              })
            }
          })
        })
        .catch(() => {

        });
    },
    coursePage(row) {
      // 跳转到课程管理页面携带数据
      this.$router.push({
        path:'/teacher_course_edit_page',
        query: row
      });
    },
    formatStartDateTime(row) {
      const timestamp = row.startDateTime;
      const date = new Date(timestamp);
      return date.toLocaleString();
    },
    formatEndDateTime(row) {
      const timestamp = row.endDateTime;
      const date = new Date(timestamp);
      return date.toLocaleString();
    },
  },
  mounted() {
    axios
      .get(
        "http://localhost:8080/SongGuo_Learning/teacher/selectCourses?id=" +
          localStorage.getItem("id"),
        {
          headers: {
            Token: localStorage.getItem("token"),
          },
        }
      )
      .then((resp) => {
        if (resp.data.message == "TOKEN_IS_NULL") {
          this.$router.push("/studentlogin");
        } else {
          console.log(resp.data);
          this.courses = resp.data.data;
        }
      });
  },
};
</script>

    
